
@charset "UTF-8";
/*$ npm i node-sass@4.0.0 --registry=https://registry.npm.taobao.org*/
html {
  font-size: 20px;
}
body {
  /*使用无衬线字体*/
  font-family: "Helvetica Neue", Helvetica, STHeiTi, sans-serif;
}
a, img {
  /*禁止长按链接与图片弹出菜单*/
  -webkit-touch-callout: none;
}
html, body {
  /*禁止选中文本*/
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  min-height: 100%;
  height: 100%;
}
button, input, optgroup, select, textarea {
  /*去掉webkit默认的表单样式*/
  -webkit-appearance: none;
}
a, button, input, optgroup, select, textarea {
  /*去掉a、input和button点击时的蓝色外边框和灰色半透明背景*/
  -webkit-tap-highlight-color: transparent;
}
input::-webkit-input-placeholder {
  /*修改webkit中input的planceholder样式*/
  color: #ccc;
}
input:focus::-webkit-input-placeholder {
  /*修改webkit中focus状态下input的planceholder样式*/
  color: #f00;
}
body {
  /*禁止IOS调整字体大小*/
  -webkit-text-size-adjust: 100% !important;
}
input::-webkit-input-speech-button {
  /*隐藏Android的语音输入按钮*/
  display: none;
}

/*清除浮动 clear & clearfix*/
.clear {
  display: block !important;
  clear: both !important;
  float: none !important;
  margin: 0 !important;
  padding: 0 !important;
  height: 0;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
}
.clearfix {
  zoom: 1;
}
.clearfix:after {
  content: "";
  display: block;
  clear: both;
  height: 0;
}
.view {
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

/*fade动画 begin*/
/*fade 进入后 ＆ 离开后 状态*/
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s ease;
}

/*fade 进入前 ＆ 离开后*/
.fade-enter, .fade-leave-active {
  opacity: 0;
}

/*slide动画 begin*/
/*slide-left 进入前 状态*/
/*slide-right 离开后 状态*/
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  transform: translate(30px, 0);
}

/*slide-left 离开后 状态*/
/*slide-right 进入前 状态*/
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  transform: translate(-30px, 0);
}

/*slide-down 进入的开始状态 ＆ 离开的结束状态*/
.slide-down-enter, .slide-down-leave-active {
  opacity: 0;
  transform: translate(0, -30px);
}

/*slide-down 进入的结束状态 ＆ 离开的开始状态*/
.slide-down-enter-active, .slide-down-leave {
  transition: all .5s ease;
}
.flex {
  /*定义为flexbox的“父元素”*/
  display: -ms-flexbox;
  display: flex;
  /*指定主轴的伸缩流方向是横向的*/
  /*指定主轴的伸缩流方向是纵向的*/
}
.flex-all__center {
    /*子元素沿主轴对齐方式居中*/
    -ms-flex-pack: center;
        justify-content: center;
    /*子元素沿侧轴对齐方式垂直居中*/
    -ms-flex-align: center;
        align-items: center;
}
.flex-column__between {
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.flex-column__around {
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.flex-row {
    -ms-flex-direction: row;
        flex-direction: row;
}
.flex-column {
    -ms-flex-direction: column;
        flex-direction: column;
}
.app-nav {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 12.5rem;
  background-color: transparent;
  color: #666;
}
.app-nav a {
    color: #666;
}
.app-nav ul, .app-nav li {
    list-style: none;
    padding: 0;
    margin: 0;
}
.view {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 12.5rem;
}



